import { Field, Picker, Popup, Toast } from "@taroify/core"
import { ArrowRight } from "@taroify/icons"
import { CustomWrapper } from "@tarojs/components"
import * as _ from "lodash"
import * as React from "react"
import { useState } from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicPicker() {
  return (
    <>
      <Toast id="toast" />
      <Picker onChange={(value) => Toast.open(`${value}`)}>
        <Picker.Toolbar>
          <Picker.Button></Picker.Button>
          <Picker.Title></Picker.Title>
          <Picker.Button></Picker.Button>
        </Picker.Toolbar>
        <Picker.Column>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
        </Picker.Column>
      </Picker>
    </>
  )
}

function DefaultPicker() {
  return (
    <>
      <Toast id="toast" />
      <Picker defaultValue="" onChange={(value) => Toast.open(`${value}`)}>
        <Picker.Toolbar>
          <Picker.Button></Picker.Button>
          <Picker.Title></Picker.Title>
          <Picker.Button></Picker.Button>
        </Picker.Toolbar>
        <Picker.Column>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
        </Picker.Column>
      </Picker>
    </>
  )
}

function PickerWithMultiColumns() {
  return (
    <>
      <Toast id="toast" />
      <Picker onConfirm={(values) => Toast.open(`${_.join(values, ",")}`)}>
        <Picker.Toolbar>
          <Picker.Button></Picker.Button>
          <Picker.Title></Picker.Title>
          <Picker.Button></Picker.Button>
        </Picker.Toolbar>
        <Picker.Column>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
        </Picker.Column>
        <Picker.Column>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
          <Picker.Option></Picker.Option>
        </Picker.Column>
      </Picker>
    </>
  )
}

function DisabledPicker() {
  return (
    <Picker>
      <Picker.Toolbar>
        <Picker.Button></Picker.Button>
        <Picker.Title></Picker.Title>
        <Picker.Button></Picker.Button>
      </Picker.Toolbar>
      <Picker.Column>
        <Picker.Option disabled></Picker.Option>
        <Picker.Option></Picker.Option>
        <Picker.Option></Picker.Option>
      </Picker.Column>
    </Picker>
  )
}

function LoadingPicker() {
  return (
    <Picker loading>
      <Picker.Toolbar>
        <Picker.Button></Picker.Button>
        <Picker.Title></Picker.Title>
        <Picker.Button></Picker.Button>
      </Picker.Toolbar>
      <Picker.Column>
        <Picker.Option></Picker.Option>
        <Picker.Option></Picker.Option>
      </Picker.Column>
      <Picker.Column>
        <Picker.Option></Picker.Option>
        <Picker.Option></Picker.Option>
        <Picker.Option></Picker.Option>
        <Picker.Option></Picker.Option>
        <Picker.Option></Picker.Option>
      </Picker.Column>
    </Picker>
  )
}

function PickerPopup() {
  const [value, setValue] = useState("")
  const [openPicker, setOpenPicker] = useState(false)

  return (
    <>
      <Field
        value={value}
        label=""
        placeholder=""
        readonly
        rightIcon={<ArrowRight />}
        onClick={() => setOpenPicker(true)}
      />
      <Popup open={openPicker} rounded placement="bottom" onClose={setOpenPicker}>
        <Popup.Backdrop />
        <Picker
          onCancel={() => setOpenPicker(false)}
          onConfirm={(values) => {
            setValue(values)
            setOpenPicker(false)
          }}
        >
          <Picker.Toolbar>
            <Picker.Button></Picker.Button>
            <Picker.Title></Picker.Title>
            <Picker.Button></Picker.Button>
          </Picker.Toolbar>
          <Picker.Column>
            <Picker.Option></Picker.Option>
            <Picker.Option></Picker.Option>
            <Picker.Option></Picker.Option>
            <Picker.Option></Picker.Option>
            <Picker.Option></Picker.Option>
            <Picker.Option></Picker.Option>
            <Picker.Option></Picker.Option>
          </Picker.Column>
        </Picker>
      </Popup>
    </>
  )
}

export default function PickerDemo() {
  return (
    <Page title="Picker " className="picker-demo">
      <Block variant="card" title="">
        <CustomWrapper>
          <BasicPicker />
        </CustomWrapper>
      </Block>
      <Block variant="card" title="">
        <CustomWrapper>
          <DefaultPicker />
        </CustomWrapper>
      </Block>
      <Block variant="card" title="">
        <CustomWrapper>
          <PickerWithMultiColumns />
        </CustomWrapper>
      </Block>
      <Block variant="card" title="">
        <CustomWrapper>
          <DisabledPicker />
        </CustomWrapper>
      </Block>
      <Block variant="card" title="">
        <CustomWrapper>
          <LoadingPicker />
        </CustomWrapper>
      </Block>
      <Block variant="card" title="">
        <CustomWrapper>
          <PickerPopup />
        </CustomWrapper>
      </Block>
    </Page>
  )
}
